<template>
  <section id="cart">
    <div class="section-info" v-if="cartItems.length === 0">
      <p>В корзине нету товаров...</p>
    </div>
    <div v-else class="cart__info">
      <div class="cart__table">
        <table class="list__table">
          <thead class="list__table__header">
            <tr class="list__table__columns">
              <th>Название</th>
              <th>Цена</th>
              <th>Количество</th>
              <th>Удалить</th>
            </tr>
          </thead>
          <tbody class="list__content" ref="table">
            <tr
              v-for="(item, index) in cartItems"
              :key="index"
              :id="`rowId${index + 1}`"
              v-bind:data-rowIndex="index"
              @dblclick.stop="$emit('open-task-dialog')"
            >
              <td class="title">
                <img :src="getImgUrl(item.src)" alt="" />
                <span>{{ item.title }}</span>
              </td>
              <td>
                <strong>{{ item.price }}</strong> грн
              </td>
              <td class="quantity">
                <button
                  :disabled="item.quantity === 0"
                  @click="item.quantity--"
                >
                  -
                </button>
                <span>{{ item.quantity }}</span>
                <button
                  :disabled="item.quantity === 10"
                  @click="item.quantity++"
                >
                  +
                </button>
              </td>
              <td>
                <a href="#" class="trash" @click.prevent="onRemoveItem(item)">
                  <img src="../assets/trash-can.svg" alt="" />
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="cart__total">
        <div class="total">
          <h2>Итого:</h2>
          <h1>{{ totalPrice }} грн</h1>
        </div>
        <button class="btn">Оформить заказ</button>
      </div>
    </div>
  </section>
</template>

<script setup>
import { ref, onMounted, computed } from "vue";

const props = defineProps({
  items: {
    type: Array,
    default: [],
  },
});

const cartItems = ref([]);

const totalPrice = computed(() => {
  return cartItems.value.reduce((res, item) => {
    res += item.quantity * item.price;
    return res;
  }, 0);
});

const onRemoveItem = (item) => {
  cartItems.value = cartItems.value.filter((i) => i.id !== item.id);
};

const getImgUrl = (src) => {
  return require(`../assets/${src}`);
};

onMounted(() => {
  const params = localStorage.getItem("cartItems");
  cartItems.value =
    params != "undefined" && params != "null" && params
      ? JSON.parse(params)
      : props.items;
});
</script>

<style lang="scss">
#cart {
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  .section-info p {
    font-size: 40px;
    color: gray;
  }
  .cart__info {
    max-width: 85%;
    margin: 0 auto;
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    @media (max-width: 1100px) {
      max-width: 100%;
      justify-content: space-evenly;
    }
    @media (max-width: 920px) {
      flex-direction: column;
    }
  }
  .cart__table {
    max-width: 65%;
    width: 100%;
    @media (max-width: 920px) {
      max-width: 85%;
    }
  }
  .cart__total {
    max-width: 27%;
    width: 100%;
    @media (max-width: 920px) {
      max-width: 270px;
      width: 100%;
      margin-top: 30px;
    }

    .total {
      display: flex;
      justify-content: space-around;
      margin-bottom: 20px;

      h1 {
        font-size: 2rem;
        font-weight: bold;
      }
      h2 {
        font-size: 1.8rem;
      }
    }
  }
  .list__table {
    width: 100%;
    border-spacing: 0 10px;
    border-collapse: collapse;
  }
  .list__table__header {
    @media (max-width: 620px) {
      display: none;
    }
  }
  .list__table__columns {
    th {
      border-bottom: 1px solid #000;
      padding: 10px 0;
    }
  }
  .list__content {
    tr {
      border-bottom: 1px dashed gray;
    }
    td {
      padding: 10px 0;
      @media (max-width: 520px) {
        max-width: 130px;
      }
    }
    .title {
      display: grid;
      grid-template-columns: repeat(2, 105px);
      justify-content: space-evenly;
      align-items: center;
      @media (max-width: 520px) {
        flex-direction: column;
        display: flex;
        height: 130px;
      }

      img {
        width: 80px;
        height: 70px;
      }
    }
    .trash {
      display: block;
      transition: 0.3s;

      &:hover {
        transform: scale(1.2);
      }

      img {
        height: 30px;
      }
    }
    .quantity {
      button {
        cursor: pointer;
        width: 30px;
        height: 30px;
        font-size: 23px;
        background: #fff;
        border: 1px solid #000;
        text-align: center;
        @media (max-width: 680px) {
          width: 20px;
          height: 20px;
          padding: 0;
          font-size: 15px;
        }
      }
      span {
        font-weight: 700;
        color: black;
        text-align: center;
        width: 40px;
        display: inline-block;
        font-size: 15px;
        @media (max-width: 680px) {
          width: 30px;
        }
      }
    }
  }
}
</style>
